import { Component, h, Host, Prop } from '@stencil/core';

@Component({
  tag: 'fle-info',
  styleUrl: 'fle-info.scss',
  shadow: false, // no shadow dom cause CMSs system need access for live preview
})
export class FleInfo {
  @Prop() headline: string;
  @Prop() category: string;
  @Prop() description: string;

  // TODO handle button
  render() {
    return (
      <Host class="fle-info">
        {this.category && <fle-category>{this.category}</fle-category>}
        {this.headline && <h2>{this.headline}</h2>}
        {this.description && <p>{this.description}</p>}
        <fle-button>Learn More</fle-button>
      </Host>
    );
  }
}
